import React from 'react';
import { Group, Anchor, Text } from '@mantine/core';

const Footer = () => {
  return (
    <footer style={{ backgroundColor: '#f4f4f4', padding: '20px', textAlign: 'center' }}>
      <Group position="center" spacing="xs">
        <Anchor href="https://linkedin.com/in/seu-perfil" target="_blank" rel="noopener noreferrer">
          LinkedIn
        </Anchor>
        <Anchor href="https://github.com/seu-perfil" target="_blank" rel="noopener noreferrer">
          GitHub
        </Anchor>
      </Group>
      <Text size="sm" color="dimmed" mt="sm">
        © 2023 Nome Completo. Todos os direitos reservados.
      </Text>
    </footer>
  );
};

export default Footer;